import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Tool from '@/components/Tool'
import Forum from '@/components/Forum'
import Shop from '@/components/Shop'
import Sub from '@/components/Sub'
import How from '@/components/How'
import DL from '@/components/Dl_link'
import Hunt from '@/components/Hunt'
import ToolManage from '@/components/tool/ToolManage'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index,
    },{ 
    	path: '/tool',
			component: Tool,
      children: [{
      	path: 'a',
//    	name:"ToolManage",
      	component: ToolManage,
      }]
    },{
      path: '/forum',
      name: 'Forum',
      component: Forum
    },{
      path: '/shop',
      name: 'Shop',
      component: Shop
    },{
      path: '/sub',
      name: 'Sub',
      component: Sub
    },{
      path: '/how',
      name: 'How',
      component: How
    },{
      path: '/dl',
      name: 'DL',
      component: DL
    },{
      path: '/hunt',
      name: 'Hunt',
      component: Hunt
    },{
    	path: '/toolmanage',
    	component: ToolManage
    }
  ]
})



