<template>
	 <div class="swiper-container">
        <div class="swiper-wrapper" v-bind:style="{height: '220px'}">
        	<a :href=item.href class="swiper-slide" v-for="item in bannerlist"><img :src="item.src" v-bind:style="{height: '220px'}"><p>{{item.title}}</p></a>
        </div>
    	<div class="swiper-pagination" v-bind:style="{bottom: '0'}"></div>
    </div>
</template>
<script>
import '../../static/css/swiper.min.css'
import '../../static/js/swiper.min.js'
export default {
  	name: 'swiper',
  	data () {
  		return {
			bannerlist:[],
			swiper: null
		}
  	},
  	methods: {
		init:function() {
			this.swiper = new Swiper(".swiper-container", {
	            pagination: ".swiper-pagination",
//	            loop: true
	        })
		},
		getData:function() {
			var Json = "";
			switch(this.$route.path) {
					case '/': Json = './static/json/news.json';
						break;
					case '/how': Json = './static/json/hs_how.json';
						break;
					case '/dl': Json = './static/json/DL_link.json';
						break;
			};
			this.$http.get(Json).then((response) => {
				this.bannerlist = response.body.bannerlist;
				this.bannerlist = this.bannerlist.slice(0,3)
			}).then(function() {
				this.init();
			})
			
		}
  	},
  	mounted:function() {
		this.getData();
  	}
  	
 }
</script>

<style>
p {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	color: #fff;
	text-align: left;
	background-color: rgba(0,0,0,0.4);
	padding-bottom: 16px;
}
</style>


