<template>
<div id="indexM">
	<ul>
		<li v-for="(item,index) in indexMainList" class="I-M-li">
			<router-link :to="item.Href">
				<div class="news">
					<div class="img"><img :src="item.src"/></div>
					<div class="describe">
						<h2>{{item.title}}</h2>
						<div class="tips">
							<span v-if="$route.path=='/'">
								{{item.Class}} 
							</span>
							<span class="lei" v-if="parseInt(new Date(new Date() - item.time)/1000/60/60/24)>0">  
								{{new Date(new Date() - item.time) .getMonth()+1}}月{{new Date(new Date() - item.time).getDate()}}日
							</span>
							<span class="lei" v-else-if=" parseInt(new Date(new Date() - item.time)/1000/60/60%24)>0">   
								{{parseInt(new Date(new Date() - item.time)/1000/60/60%24)}}小时前
							</span>
							<span class="lei" v-else-if=" parseInt(new Date(new Date() - item.time)/1000/60%60)>0"> 
								{{parseInt(new Date(new Date() - item.time)/1000/60%60)}}分钟前
							</span>
							<span class="IM_num">{{item.com_num}}</span>
						</div>
					</div>
				</div>
			</router-link>
		</li>
	</ul>
</div>
</template>
<script>
export default {
	name:'IndexMain',
	data:function() {
		return {
			indexMainList:[]
		}
	},
	methods: {
		getData:function() {
			var Json = "";
			switch(this.$route.path) {
					case '/': Json = './static/json/news.json';
						break;
					case '/how': Json = './static/json/hs_how.json';
						break;
					case '/dl': Json = './static/json/DL_link.json';
						break;
			};
			this.$http.get(Json).then((response) => {
				this.indexMainList = response.body.bannerlist;
			})
		}
	},
  	mounted:function() {
		this.getData();
  	}
}
</script>
<style>
/*#indexM {
	margin-bottom: 64px;
}*/
.I-M-li {
	padding: 5px 8px;
	border-bottom: 1px solid #ecf1f5;
}
.news {
	display: flex;
}
.news .img {
	width: 132px;
	height: 80px;
	margin-right: 4vw;
}
.news .img img{
	width: 100%;
	height: 100%;
}
.news .describe {
	flex:1;
	text-align: left;
}
.news .describe h2 {
	font-size: 17px;
	height: 58px;
	color: #4a4a4a;
}
.news .describe .tips {
	height: 16px;
	display: flex;
	color: #9b9b9b;
}
.news .describe .tips .lei {
	flex: 1;
}
.news .describe .tips .IM_num {
	padding-left: 20px;
	height: 16px;
	background: url(/static/img/show-comment.png) no-repeat;
	background-position: 0 0px;
	background-size: contain;
}
</style>



