<template>
	<footer id="footer" v-show="!($route.path === '/toolmanage')">
		<ul>
			<li v-for="(item,index) in footerList" v-bind:class="['foo-icon-'+(index+1),{active:(($route.path === item.path)||item.ac)}]" v-on:click="btn">
				<router-link :to="item.path">{{item.text}}</router-link>
			</li>
		</ul>
	</footer>  
</template>

<script>
export default {
	name: 'footer',
	data: function() {
		return {
			footerList:[{
				path:'/',
				text:"资讯",
				ac:true
			},{
				path:'/tool',
				text:"工具",
				ac:false
			},{
				path:'/forum',
				text:"论坛",
				ac:false
			},{
				path:'/shop',
				text:"商城",
				ac:false
			}]
		}
	},
	methods: {
		btn() {
			var _this = this;
			this.footerList[0].ac = true;
			if(!(this.$route.path==this.footerList[0].path)) {
				_this.footerList[0].ac = false
			}
		}
	}
}	
</script>

<style>
#footer {
	height: 64px;
	width: 100%;
	margin-top: 10px;
	position: fixed;
	left:0;
	bottom:0;
}
#footer ul {
	width:100%;
	height: 100%;
	border-top:1px solid #d9d9d9;
	background-color: #fff;
    display: flex;
    font-size: 1.4rem;
}
#footer ul li {
	flex:1;
	text-align: center;
}
#footer ul li a {
	height: 100%;
	padding: 4px 10px 2px;
	display: block;
	color:#3b68b8;
	background: red;
	line-height: 90px;
	background: url(/static/img/footer.png) no-repeat;
	background-position: center 10px;
}
#footer ul .foo-icon-1.active a {
	background-position: center -50px;
}
#footer ul .foo-icon-2 a {
	background-position: center -119px;
}
#footer ul .foo-icon-2.active a {
	background-position: center -192px;
}
#footer ul .foo-icon-3 a {
	background-position: center -257px;
}
#footer ul .foo-icon-3.active a {
	background-position: center -321px;
}
#footer ul .foo-icon-4 a {
	background-position: center -378px;
}
#footer ul .foo-icon-4.active a {
	background-position: center -431px;
}
</style>


