<template>
	<header id="header">
		<div class="user"><a href="javascript:void(0)" v-on:click="userK"></a></div>
		<div class="user returnBtn" v-show="$route.path === '/toolmanage'"><a href="javascript:void(0)" v-on:click="routerBack"></a></div>
		<div class="user-box" v-show="userKG" v-on:click="userK">
			<div class="userBox" v-on:click.stop>
				<div class="userA">
					<a href="javascript:void(0)">
						<div class="userhead"><img src="/static/img/lvtu/lvtu.jpg"/><span>LV11</span></div>
						<div class="userName">
							<h2>风之旅途</h2>
							<span>营火：546</span>
						</div>
					</a>
				</div>
				<div>
					<ul class="userBox-Btn">
						<li v-for="(item,index) in userBox1" v-bind:class="'userBox-Btnli'+(index+1)">
							<a :href="item.Href">{{item.txt}}</a>
						</li>
					</ul>
				</div>
				<div>
					<ul class="userBox-Btn userBox-Btn1">
						<li v-for="(item,index) in userBox2" v-bind:class="'userBox-Btn_2li'+(index+1)">
							<a :href="item.Href">{{item}}</a>
						</li>
					</ul>
				</div>
			</div>
		</div>
		<div class="header-main-right" v-show="$route.path === '/shop'">商城</div>
		<div class="header-main-right" v-show="$route.path === '/forum'">论坛列表</div>
		<div class="header-main-right" v-show="$route.path === '/tool'">我的工具</div>
		<div class="header-main-right" v-show="$route.path === '/toolmanage'">工具管理</div>
		<div class="header-main">
			<!--<ul v-bind:style="{width:ulWidth}">
				<li v-for="(item,index) in headerList" v-bind:class="{header_active:($route.path === item.Href)}">
					<router-link :to="item.Href">{{item.title}}</router-link>
				</li>
			</ul>-->
		</div>
		<div class="search"><a href="#/hunt"></a></div>
		<div class="shopping" v-show="$route.path === '/shop'">
			<a href="javascript:void(0)" class="shopIndent"></a>
			<a href="javascript:void(0)" class="shopCart"></a>
		</div>
		<div class="tool" v-show="$route.path === '/tool'">
			<a href="#/toolmanage">管理</a>
		</div>
		<div class="tool" v-show="$route.path === '/toolmanage'">
		</div>
	</header>
</template>

<script>
export default {
	name:'header',
	data:function() {
		return {
			headerList:[{
				Href:'/sub',
				title:'订阅'
			},{
				Href:'/',
				title:'资讯'
			}],
			userBox1:[{
				txt:"我的播放列表",
				Href:"/",
			},{
				txt:"消息&通知",
				Href:"/",
			},{
				txt:"我的收藏",
				Href:"/",
			},{
				txt:"草稿箱",
				Href:"/",
			},{
				txt:"任务中心",
				Href:"/",
			},{
				txt:"设置",
				Href:"/",
			}],
			userBox2:["切至夜间模式","切至3/4G无图"],
			userKG:false,
			ulWidth:0
		}
	},
	methods:{
		userK:function() {
			this.userKG = !this.userKG
		},
		routerBack(){
	        this.$router.go(-1);
	   	},
	   	getData:function() {
	   		this.$http.get("./static/json/list.json").then((response) => {
	   			this.headerList = this.headerList.concat(response.body.list);
	   		})
	   		.then(function() {
	   			this.ulWidth = 17.5 * this.headerList.length + 'vw';
	   		})
	   	}
	},
	mounted:function() {
		this.getData()
	}
}
</script>

<style>
#header {
	position: fixed;
	/*display: flex;*/
	height: 60px;
	width: 100vw;
	top: 0;
	left: 0;
	z-index: 10;
	padding: 0 15vw;
	overflow: hidden;
}

#header .user {
	width: 15vw;
	height: 60px;
	background: #fff;
	position: absolute;
	left: 0;
	top: 0;
}
#header .user a {
	background: url(/static/img/header.png) no-repeat;
	background-position: center 15px;
	display: block;
	width: 100%;
	height: 100%;
}
#header .returnBtn {
	z-index: 10;
}
#header .returnBtn a {
	background: url(/static/img/return-btn.png) no-repeat;
	background-size: 40px 40px;
	background-position: center 12px;
}
#header .user-box {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	background: rgba(0,0,0,0.5);
	z-index: 11;
}
#header .user-box .userBox {
	width: 50vw;
	height: 100vh;
	background: #fff;
}
.userBox .userBox-Btn {
	border-bottom: 4px solid #ECF1F5;
} 
.userBox .userBox-Btn1 {
	border-bottom: none;
} 
.userBox .userBox-Btn li{
	/*padding-left: 18px;*/
	margin-left: 10px;
	background: url(/static/img/user.png) no-repeat;
	background-size: 20px; 
	background-position: 0 12px;
	text-align: left;
	padding: 10px 0px 10px 28px;
}
.userBox .userBox-Btn .userBox-Btnli2 {
	background-position: 0 -17px;
}
.userBox .userBox-Btn .userBox-Btnli3 {
	background-position: 0 -46px;
}
.userBox .userBox-Btn .userBox-Btnli4 {
	background-position: 0 -74px;
}
.userBox .userBox-Btn .userBox-Btnli5 {
	background-position: 0 -103px;
}
.userBox .userBox-Btn .userBox-Btnli6 {
	background-position: 0 -133px;
}
.userBox .userBox-Btn .userBox-Btn_2li1 {
	background-position: 0 -170px;
}
.userBox .userBox-Btn .userBox-Btn_2li2 {
	background-position: 0 -200px;
}
.userBox .userBox-Btn li a{
	display: block;
	font-size: 14px;
	color: #4A4A4A;
}
#header .user-box .userBox .userA a {
	display: flex;
	background: url(/static/img/swiper_4.jpg);
	height: 120px;
	margin-bottom: 10px;
}
#header .user-box .userBox .userA a .userhead {
	position: relative;
	width: 60px;
	height: 60px;
	border: 6px solid #345e9c;
	border-radius: 100px;
	margin: 24px 12px;
} 
#header .user-box .userBox .userA a .userhead img {
	width: 100%;
	height: 100%;
	border-radius: 100px;
}
#header .user-box .userBox .userA a .userhead span {
	position: absolute;
	bottom: -16px;
	left: 10px;
	padding: 2px 6px;
	border-radius: 6px;
	background: #33589b;
	color: #fff;
}
#header .user-box .userBox .userA a .userName {
	flex: 1;
	color: #FFFFFF;
	margin: 32px 0;
	text-align: left;
}
#header .user-box .userBox .userA a .userName h2 {
	font-size: 16px;
}
#header .header-main {
	width: 70vw;
	height: 75px;
	background: #fff;
	overflow-y: hidden;
	overflow-x: scroll;
}
#header .header-main-right {
	width: 70vw;
	height: 60px;
	background: #fff;
	line-height: 60px;
	font-size: 24px;
	color: #3b68b8;
	z-index: 10;
}
#header .header-main ul {
	width: 122.5vw;
	height: 60px;
}
#header .header-main ul li {
	float: left;
	width: 17.5vw;
	line-height: 60px;
	color: #3866bb;
}
#header .header-main ul li a {
	display: block;
	height: 100%;
	width: 100%;
}
#header .search {
	width: 15vw;
	height: 60px;
	background: #fff;
	position: absolute;
	left: 85vw;
	top: 0;
}
#header .search a {
	background: url(/static/img/header.png) no-repeat;
	background-position: center -198px;
	display: block;
	width: 100%;
	height: 100%;
}
.header_active {
	font-size:20px
}
#header .shopping {
	width: 30vw;
	height: 60px;
	background: #fff;
	position: absolute;
	left: 70vw;
	top: 0;
	z-index: 10;
	display: flex;
}
#header .shopping a {
	flex: 1;
	width: 60px;    
	height: 60px;
	display: inline-block;
	background: url(/static/img/header.png) no-repeat;
	background-position: center -60px;
	margin-right: 8px;
	margin-left: 8px;
}
#header .shopping .shopCart {
	background-position: center -124px;
}
#header .tool {
	width: 20vw;
	height: 60px;
	background: #fff;
	position: absolute;
	left: 80vw;
	top: 0;
	z-index: 10;
}
#header .tool a {
	display: inline-block;
	width: 100%;
	height: 100%;
	text-align: center;
	line-height: 60px;
	font-size: 18px;
	color: #3b68b8;
}
</style>


